for (let i = 0; i < 10; i++) {
    var sex, canteen;
    if (i % 2 == 0) {
        sex = "";
    } else {
        sex = "";
    }
    if (i < 2) {
        canteen = "1";
    } else if (i < 4) {
        canteen = "2";
    } else if (i < 6) {
        canteen = "3";
    } else if (i < 8) {
        canteen = "4";
    } else {
        canteen = "5";
    }
    data.push({
