                this.isIntoAndOut = false;
            }
        },
        // 
        mxWarehouse(e) {
            console.log(e);
            this.warehouseId = e;
        },
        // 
        inputInfoCancel() {
            this.inputInfoVisible = false;
        },
        inputInfoPreview(file) {
            this.inputInfoImage = file.url || file.thumbUrl;
            this.inputInfoVisible = true;
        },
        inputInfoChange(info) {
            let fileList = [...info.fileList];

            // 1.
            // 3
            fileList = fileList.slice(-2);

            // 2. 
            fileList = fileList.map(file => {
                if (file.response) {
                    // file.url
                    file.url = file.response.url;
                }
                return file;
            });

            this.inputInfoList = fileList;
        },
        // 
        inputInfosCancel() {
            this.inputInfosVisible = false;
        },
        inputInfosPreview(file) {
            this.inputInfosImage = file.url || file.thumbUrl;
            this.inputInfosVisible = true;
        },
        inputInfosChange(info) {
            let sedList = [...info.fileList];

            // 1.
            // 3
            sedList = sedList.slice(-2);

            // 2. 
            sedList = sedList.map(file => {
                if (file.response) {
                    // Component will show file.url as link
                    file.url = file.response.url;
                }
                return file;
            });

            this.inputInfosList = sedList;
        },
        // 
        secondSubmit(e) {
            e.preventDefault();
            this.myForm.validateFields((err, values) => {
                if (!err) {
                    console.log("Received values of myForm: ", values);
                    console.log(this.intoShow);
                    console.log(this.contactMode);
                    console.log(this.intoMode);
                    Object.assign(values, { 'contactMode': this.contactMode, 'intoMode': this.intoMode });
                    this.mxhcData = values;
                    console.log(this.mxhcData);

                    this.isInputInfo = false;
                    this.isSecond = true;
                }
            });
        },
        secondSubmits(e) {
            // this.isInputInfo = false;
            // this.isSecond = true;
            e.preventDefault();
            this.myForm.validateFields((err, values) => {
                if (!err) {
                    console.log("Received values of myForm: ", values);
                }
            });
        },
        backSecond() {
            this.isInputInfo = true;
            this.isSecond = false;
        },
        // 
        mxPrebtn() {
            this.isInputInfo = true;
            this.isSecond = false;
            // this.mxhcData
            // this.myForm.setFieldsValue({
            //     canteen: this.mxhcData.canteen,
            //     originMode: this.mxhcData.originMode,
            //     price: this.mxhcData.price,
            //     number: this.mxhcData.number,
            //     warehouse: this.mxhcData.warehouse,
            //     originName: this.mxhcData.originName,
            //     originAddress: this.mxhcData.originAddress,
            // });
            // this.intoMode = this.mxhcData.intoMode;
            // this.contactMode = this.mxhcData.contactMode;

            // let imgArr = []
            // val.photos_src.forEach((item, i) => {
            //     let obj = {
            //         uid: item.id,
            //         name: 'xxx.png',
            //         response: {
            //             data: {
            //                 id: parseInt(item.id),
            //                 url: item.url
            //             }
            //         },
            //         status: 'done',
            //         url: item.url
            //     }
            //     imgArr.push(obj)
            // });
            // this.imgsLists = imgArr;
        },
        // 
        disabledStartDate(startValue) {
            const endValue = this.endValue;
            if (!startValue || !endValue) {
                return false;
            }
            return startValue.valueOf() > endValue.valueOf();
        },
        disabledEndDate(endValue) {
            const startValue = this.startValue;
            if (!endValue || !startValue) {
                return false;
            }
            return startValue.valueOf() >= endValue.valueOf();
        },
        handleStartOpenChange(open) {
            if (!open) {
                this.endOpen = true;
            }
        },
        handleEndOpenChange(open) {
            this.endOpen = open;
        },
        //  
        mxState(e) {
            this.state = e;
            this.stateInfo = e;
        },
        // 
        handleTableChange(pagination, filters, sorter) {
            this.pagination.pageSize = pagination.pageSize;
            this.pagination.current = pagination.current;
            this.loadingRecord = true;
            this.getIntoRecord('', '', '', '', '');
        },
        // 
        inquiryData() {
            this.loadingRecord = true;
            this.getIntoRecord(this.canteenInfoId, this.mxClassValInfo, this.state, this.startTimeMeal, this.endTimeMeal);
        },
        // 
        inquiryMealReset() {
            this.startValue = null;
            this.endValue = null;
            this.startTimeMeal = '';
            this.endTimeMeal = '';
            this.canteenInfoKey = '';
            this.canteenInfoId = 0;
            this.mxClassValInfo = '';
            this.state = '0';
            this.stateInfo = '';

            this.loadingRecord = true;
            this.getIntoRecord('', '', '', '', '');
        },
        // 
        mxShowInfo(id) {
            this.$get("api/school/record/" + id).then(res => {
                if (res.data.code == 0) {
                    // record_origin1 2',
                    // record_state1234',
                    // this.isNormal = true;    //
                    // this.isNormal = true;    //
                    // this.isNotInto = true;    //
                    // this.isOther = true;    //
                    // this.isOtherOut = true;    //  
                    res.data.data.forEach(item => {
                        this.acceptanceShow = item;
                        if (item.record_origin == 1) {
                            if (item.record_state == 1) {
                                this.isNormal = true; //
                            } else if (item.record_state == 3) {
                                this.isNormal = true;  //
                            } else {
                                this.isNotInto = true; //
                            }
                        } else {
                            if (item.record_state == 1) {
                                this.isOther = true;    //
                            } else {
                                this.isOtherOut = true;    //  
                            }
                        }
                    })
                } else {
                    this.$message.success(res.data.msg);
                }
            })
            this.isShowInfo = true;
        },
        // 
        backShowInfo() {
            this.isShowInfo = false;
            this.isNormal = false;    //
            this.isNormal = false;    //
            this.isNotInto = false;    //
            this.isOther = false;    //
            this.isOtherOut = false;    //  
        }
    },
}
</script>

